import React from 'react';
import T from 'prop-types';
import styled, { withTheme } from 'styled-components';
import pick from 'lodash/pick';
import pickFromTheme from '@splunk/themes/pick';
import variables from '@splunk/themes/variables';
import { _ } from '@splunk/ui-utils/i18n';
import { toDimension } from '@splunk/visualizations-shared/style';
import SizeAwareWrapper from '@splunk/visualizations-shared/SizeAwareWrapper';
import Message from '@splunk/visualizations-shared/Message';
import {
    GAUGE_THICKNESS,
    assignGaugeColor,
    getGaugeDimensions,
    getFillBarDimensions,
    getFillerGaugeValueMarkerPositions,
} from '@splunk/visualizations/common/gaugeUtils';
import MajorTicks from '@splunk/visualizations/common/GaugeMajorTicks';
import ValueMarker from '@splunk/visualizations/common/GaugeValueMarker';
import { withSanitizedProps } from '@splunk/visualizations-shared/SanitizeProps';
import { config } from '@splunk/visualizations/MarkerGauge';

const Container = styled.div`
    overflow: hidden;
    ${props => toDimension(pick(props, ['width', 'height']))};
    background-color: ${props => props.backgroundColor};
`;

const FillerGaugeWrapper = styled.div`
    display: flex;
    justify-content: center;
    flex-direction: row;
    width: 100%;
    height: 100%;
    background-color: ${props => props.backgroundColor};
`;

const FillerGaugeContainer = styled.div`
    overflow: hidden;
    position: relative;
    ${props => toDimension(pick(props, ['width', 'height']))};
`;

const FillerGaugeSVG = styled.svg`
    ${props => toDimension(pick(props, ['width', 'height']))};
`;

const BackgroundBar = styled.rect.attrs({ 'data-test': 'background-bar-rect' })`
    fill: ${pickFromTheme({
        enterprise: variables.backgroundColorHover,
        prisma: {
            light: variables.contentColorActive, // TBD
            dark: variables.neutral100, // interactiveColorOverlayHover made it almost impossible to contrast with the surrounding background
        },
    })};
`;

const FillBar = styled.rect.attrs({ 'data-test': 'fill-bar-rect' })`
    fill: ${props => props.color};
`;

const FillerGauge = ({ ...props }) => {
    const {
        width,
        height,
        value,
        gaugeColor,
        min,
        max,
        orientation,
        majorUnit,
        showLabels,
        showValue,
        usePercentageRange,
        usePercentageValue,
        backgroundColor,
    } = props;

    const majorTickFillColor = config.themes.majorTickFillColor(props);
    const majorTickStrokeColor = config.themes.majorTickStrokeColor(props);
    const valueMarkerFillColor = config.themes.valueMarkerFillColor(props);
    const valueMarkerLabelFillColor = config.themes.valueMarkerLabelFillColor(props);

    const renderVisualization = ({ width: containerWidth, height: containerHeight }) => {
        // todo: need change the size when get design for smaller space
        // 58 is the title & description header height in dashboard visualization
        if (
            ((containerWidth < 200 || containerHeight + 58 < 200) && orientation === 'vertical') ||
            ((containerWidth < 200 || containerHeight < 100) && orientation === 'horizontal')
        ) {
            return (
                <Message
                    width={containerWidth}
                    height={containerHeight}
                    message={_('Too small to render content')}
                    level="info"
                />
            );
        }

        const { gaugeLength, gaugeStartX, gaugeStartY, gaugeWidth, gaugeHeight } = getGaugeDimensions({
            containerWidth,
            containerHeight,
            orientation,
        });
        const { fillBarLength, fillBarX, fillBarY, fillBarWidth, fillBarHeight } = getFillBarDimensions({
            gaugeLength,
            gaugeStartX,
            gaugeStartY,
            gaugeWidth,
            gaugeHeight,
            value,
            min,
            max,
            orientation,
        });
        const { valueMarkerX, valueMarkerY } = getFillerGaugeValueMarkerPositions({
            fillBarX,
            fillBarY,
            fillBarLength,
            orientation,
        });

        return (
            <FillerGaugeWrapper backgroundColor={backgroundColor}>
                <FillerGaugeContainer width={containerWidth} height={containerHeight}>
                    <FillerGaugeSVG width={containerWidth} height={containerHeight}>
                        <BackgroundBar
                            x={gaugeStartX}
                            y={gaugeStartY}
                            width={gaugeWidth}
                            height={gaugeHeight}
                        />
                        <MajorTicks
                            width={gaugeWidth}
                            height={gaugeHeight}
                            startX={gaugeStartX}
                            startY={gaugeStartY}
                            min={min}
                            max={max}
                            majorUnit={majorUnit}
                            orientation={orientation}
                            showLabels={showLabels}
                            usePercentageRange={usePercentageRange}
                            majorTickFillColor={majorTickFillColor}
                            majorTickStrokeColor={majorTickStrokeColor}
                        />
                        <FillBar
                            x={fillBarX}
                            y={fillBarY}
                            width={fillBarWidth}
                            height={fillBarHeight}
                            color={gaugeColor || assignGaugeColor({ value, min, max })}
                        />
                        <ValueMarker
                            x={valueMarkerX}
                            y={valueMarkerY}
                            length={GAUGE_THICKNESS}
                            min={min}
                            max={max}
                            value={value}
                            orientation={orientation}
                            showValue={showValue}
                            usePercentageValue={usePercentageValue}
                            valueMarkerFillColor={valueMarkerFillColor}
                            valueMarkerLabelFillColor={valueMarkerLabelFillColor}
                        />
                    </FillerGaugeSVG>
                </FillerGaugeContainer>
            </FillerGaugeWrapper>
        );
    };

    return (
        <Container width={width} height={height} backgroundColor={backgroundColor}>
            <SizeAwareWrapper>
                {containerDimension => renderVisualization(containerDimension)}
            </SizeAwareWrapper>
        </Container>
    );
};

FillerGauge.propTypes = {
    width: T.oneOfType([T.string, T.number]),
    height: T.oneOfType([T.string, T.number]),
    value: T.number,
    gaugeColor: T.string,
    min: T.number,
    max: T.number,
    // valueFieldName: T.string, // todo: need add it back when displaying name later
    // gaugeColorFieldName: T.string, // todo: may need add it back may not
    orientation: T.oneOf(['horizontal', 'vertical']),
    majorUnit: T.oneOfType([T.string, T.number]),
    showLabels: T.bool,
    showValue: T.bool,
    usePercentageRange: T.bool,
    usePercentageValue: T.bool,
    backgroundColor: T.string,
};

FillerGauge.defaultProps = {
    width: '100%',
    height: 250,
    orientation: 'vertical',
    min: 0,
    max: 100,
    majorUnit: 'auto',
    showLabels: true,
    showValue: true,
    usePercentageRange: false,
    usePercentageValue: false,
    backgroundColor: 'transparent',
};

export default withTheme(withSanitizedProps(FillerGauge));
